








































import Vue from 'vue'
import Page from '@/components/Page.vue'

export default Vue.extend({
  components: { Page },
  data() {
    return {
      isVisible: false,
      type: 'ios',
      title: '',
      content: '',
      cancelText: '',
      okText: '',
      showCancel: true,
    }
  },
  methods: {
    showDialog(type: string) {
      switch (type) {
        case 'ios1':
          this.type = 'ios'
          this.title = '弹窗标题'
          this.content =
            '弹窗内容，告知当前状态、信息和解决方法，描述文字尽量控制在三行内'
          this.cancelText = '辅助操作'
          this.okText = '主操作'
          this.showCancel = true
          this.isVisible = true
          break
        case 'ios2':
          this.type = 'ios'
          this.title = ''
          this.content =
            '弹窗内容，告知当前状态、信息和解决方法，描述文字尽量控制在三行内'
          this.okText = '知道了'
          this.showCancel = false
          this.isVisible = true
          break
        case 'android1':
          this.type = 'android'
          this.title = '弹窗标题'
          this.content =
            '弹窗内容，告知当前状态、信息和解决方法，描述文字尽量控制在三行内'
          this.cancelText = '辅助操作'
          this.okText = '主操作'
          this.showCancel = true
          this.isVisible = true
          break
        case 'android2':
          this.type = 'android'
          this.title = ''
          this.content =
            '弹窗内容，告知当前状态、信息和解决方法，描述文字尽量控制在三行内'
          this.cancelText = '辅助操作'
          this.okText = '主操作'
          this.showCancel = true
          this.isVisible = true
          break
        case 'ios1F':
          this.$dialog.show({
            type: 'ios',
            title: '弹窗标题',
            content:
              '弹窗内容，告知当前状态、信息和解决方法，描述文字尽量控制在三行内',
            cancelText: '辅助操作',
            okText: '主操作',
            onCancel: () => {
              this.$toast.info('点击辅助操作')
            },
            onOk: () => {
              this.$toast.info('点击主操作')
            },
          })
          break
        case 'ios2F':
          this.$dialog.show({
            type: 'ios',
            title: null,
            content:
              '弹窗内容，告知当前状态、信息和解决方法，描述文字尽量控制在三行内',
            showCancel: false,
            okText: '知道了',
          })
          break
        case 'android1F':
          this.$dialog.show({
            type: 'android',
            title: '弹窗标题',
            content:
              '弹窗内容，告知当前状态、信息和解决方法，描述文字尽量控制在三行内',
            cancelText: '辅助操作',
            okText: '主操作',
          })
          break
        case 'android2F':
          this.$dialog.show({
            type: 'android',
            title: null,
            content:
              '弹窗内容，告知当前状态、信息和解决方法，描述文字尽量控制在三行内',
            cancelText: '辅助操作',
            okText: '主操作',
          })
          break
      }
    },
    handleCancel() {
      this.isVisible = false
    },
    handleOk() {
      this.isVisible = false
    },
  },
})
